import { atom, selector } from "recoil";

// Enums

//  TypeScript    .
// enum        .
//            . TypeScript  -  .

//   (Numeric enums)
// enum Direction {
// Up = 1,
// Down,
// Left,
// Right,
// }

//   (String enums)
// enum Direction {
// Up = "UP",
// Down = "DOWN",
// Left = "LEFT",
// Right = "RIGHT",
// }

export enum Categories {
    "TO_DO" = "TO_DO",
    "DOING" = "DOING",
    "DONE" = "DONE",
    "DELETE" = "DELETE",
    //  "DONE"   enum   .  string     
}

export interface ITodo {
    text: string;
    id: number;
    category: Categories;
}

export const categoryState = atom({
    key: "category",
    default: Categories.TO_DO,
});

export const todoState = atom<ITodo[]>({
    key: "todo",
    default: [],
});

// selector    state  
// atom  output 
// state    output

export const todoSelector = selector({
    key: "todoSelector",
    // get func  atom  
    // get: (option) =>   
    get: ({ get }) => {
        const todos = get(todoState);
        const category = get(categoryState);
        return todos.filter((todo) => todo.category === category);
    },
});
